* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

#root {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #4f0000;
  background-image: url("https://transparenttextures.com/patterns/clean-gray-paper.png");
}

button:hover, button:focus{
  box-shadow: 0px 0px 8px 1px red;
  text-shadow: 0px 0px 2px red;
}

button {
  background-color: #4f0000;
  background: url('https://images.unsplash.com/photo-1476370648495-3533f64427a2?w=600&auto=format&fit=crop&q=60&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8MTh8fGRhcmt8ZW58MHx8MHx8fDA%3D');
  box-shadow: 0px 0px 8px 1px white;
  background-size: cover;
  border: none;
  border-radius: 10%;
  color: transparent;
  text-shadow: 0px 0px 2px white;
  padding: 23px 60px;
  font-size: 30px;
  cursor: pointer;
}